export class Calendar {
	        firstDayOfWeek: number;
            monthNames: string []; 
            monthNamesShort: string [];
            dayNames: string [];
            dayNamesShort: string [];
            dayNamesMin: string [];
            today: string;
            clear: string;
	constructor(){
            this.firstDayOfWeek= 1,
            this.monthNames= ['Stycze','Luty','Marzec','Kwiecie','Maj','Czerwiec','Lipiec','Sierpie','Wrzesie','Padziernik','Listopad','Grudzie'],
            this.monthNamesShort=['Sty','Lut','Mar','Kwi','Maj','Cze', 'Lip','Sie','Wrz','Pa','Lis','Gru'],
            this.dayNames=['Niedziela','Poniedziaek','Wtorek','roda','Czwartek','Pitek','Sobota'],
            this.dayNamesShort=['Nie','Pon','Wt','r','Czw','Pt','So'],
            this.dayNamesMin=['N','P','W','','Cz','P','S'],
            this.today='Today',
            this.clear='Clear'
    }
}
